import React, { useRef } from "react";
import {
  OrbitControls,
  useGLTF,
  useTexture,
  Center,
  Sparkles,
} from "@react-three/drei";
import { useFrame } from "@react-three/fiber";
import fragment from "./shaders/portal/fragment";
import vertex from "./shaders/portal/vertex";
import * as THREE from "three";

function Experience() {
  const { nodes } = useGLTF("./model/portal.glb");
  console.log(nodes);

  const texture = useTexture("./model/baked.jpg");
  texture.flipY = false;
  console.log(texture);

  useFrame((state, delta) => {
    materialRef.current.uniforms.uTime.value += delta * 3;
  });

  const materialRef = useRef();

  return (
    <>
      <OrbitControls makeDefault />
      <Center>
        <mesh geometry={nodes.baked.geometry}>
          <meshBasicMaterial map={texture} />
        </mesh>
        <mesh
          geometry={nodes.poleLightA.geometry}
          position={nodes.poleLightA.position}
        >
          <meshBasicMaterial color="#ffffe5" />
        </mesh>
        <mesh
          geometry={nodes.poleLightB.geometry}
          position={nodes.poleLightB.position}
        >
          <meshBasicMaterial color="#ffffe5" />
        </mesh>
        <mesh
          geometry={nodes.portalLight.geometry}
          position={nodes.portalLight.position}
          rotation={nodes.portalLight.rotation}
        >
          <shaderMaterial
            ref={materialRef}
            vertexShader={vertex}
            fragmentShader={fragment}
            uniforms={{
              uTime: {
                value: 0,
              },
              uColorStart: {
                value: new THREE.Color("#ffffff"),
              },
              uColorEnd: {
                value: new THREE.Color("#000000"),
              },
            }}
          />
        </mesh>
        <Sparkles
          size={6}
          scale={[4, 2, 4]}
          position={[0, 1, 0]}
          speed={0.2}
          count={40}
        />
      </Center>
    </>
  );
}

export default Experience;
